.rating__container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  gap: 1.4em;
}

.rating__container .rating__progressbar-container {
  width: 5rem;
  height: 5rem;
}

.rating__container .rating__progressbar-container .rating__progressbar {
  font-family: "Inter", sans-serif;
  font-weight: 700;
}

.rating__container p {
  color: rgba(255, 255, 255, 0.75);
  font-size: 1.2rem;
}
